body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.header .btn{
  width: 200px;
  height: 70px;
  border-radius: 12px;
  border: none;
  background-color: rgb(127, 173, 243);
  font-size: 16px;
  font-weight: 500;
  color: black;
  outline: none;
  cursor: pointer;
}
.header .btn:hover{
  background:rgb(167, 197, 241);
  box-shadow: 0px 0px 7px 1px rgba(55, 128, 238, 0.8);
  color: rgb(30, 71, 119);
  font-weight: bold;
}

.header .logo{
  width: 200px;
  height: 200px;
  cursor: pointer;
}


.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid blue;
  border-color: blue transparent blue transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.container{
  display: flex;
  flex-direction: column;

}
.table{
  display: block;
  margin: 0 auto;
  width: 1200px;
  overflow: auto;
  padding-bottom: 5px;
  text-align: center; 
}


table{
  border: 2px solid blue;
  border-collapse: collapse;
  width: 1500px;
  margin: 0 auto;
}

tr:nth-child(1){
  background-color: rgb(177, 198, 245);
}
tr:nth-child(1) th{
  padding: 10px 50px;
}
td{
  border: 1px solid blue;
  text-align: center;
  padding: 15px;
}

th{
  border: 1px solid rgb(4, 4, 109);
  text-align: center;
 }

.date-td{
  padding: 15px 70px !important;
}

.loading{
  align-self: center;
}

/*.test-table{
}*/

.for-status{
  display: block;
  width: 20px;
  height: 20px;
  border: 1px solid rgb(2, 97, 2);
  border-radius: 5px;
  margin: 0 auto;
  transition: 0.3s;
}
.checkbox{
  opacity: 0;
}
.checked{
  background-image: url(checked.jpg)!important;
  background-size: contain;
  background-color: transparent !important;
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid blue;
}
.unchecked{
  border: 1px solid rgb(2, 97, 2);
  background-color:rgb(124, 245, 124);
  background-image: url();
}


.for-search{
  position: relative;
  margin-top: 30px;
  margin-left: 70%;
}
.search-box{
  display: flex;
  flex-direction: row;

}
.search-box input{
  width: 140px;
  height: 40px;
  border: 1px solid rgba(154, 154, 219, 0.6);
  border-right: none;
  outline: none;
  text-indent: 10px;
}
.search-box .btn{
  width: 50px;
  height: 40px;
  outline: none;
  border: 1px solid rgba(154, 154, 219, 0.6);
  border-left: none;
  background-image: url(loop.png);
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}
.add-row{
  width: 200px;
  height: 50px;
  border-radius: 10px;
  align-self: center;
  margin-top: 50px;
  background-color: rgb(104, 154, 230);
  outline: none;
  border: 1px solid rgba(154, 154, 219, 0.6);
}
.add-row:hover{
  background:rgb(167, 197, 241);
  box-shadow: 0px 0px 7px 1px rgba(55, 128, 238, 0.8);
  color: rgb(30, 71, 119);
  font-weight: bold;
  cursor: pointer;
}

.new-user{
  display: flex;
  flex-direction: column;
  align-self: center;
  width: 350px;
  margin-top: 50px;
  border-radius: 7px;
  padding: 25px;
  box-shadow: 0px 0px 7px 2px rgba(55, 128, 238, 0.8);
}
.red{
  border: 1px solid red !important;
  box-shadow: 0px 0px 7px 1px rgba(228, 48, 25, 0.8) !important;
}
.new-user input{
  border: 1px solid rgb(82, 174, 228);
  padding: 10px 5px;
  outline: none;
  text-indent: 15px;
}
.new-user input::placeholder{
  color: rgb(28, 121, 184);
  opacity: 0.8;
}
.new-user .close-modal{
  width: 70%;
  height: 35px;
  background-color: rgb(57, 156, 223);
  color: #fff;
  font-weight: bold;
  border-radius: 5px;
  outline: none;
  border: none;
  align-self: center;
  cursor: pointer;
}
.new-user .close-modal:hover{
  background-color: rgb(90, 164, 214);
  box-shadow: 0px 0px 7px 2px rgba(55, 128, 238, 0.8);
}

.td_picker{
  position: relative;
}
.for-calendar{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
}
.for-number{
  position: absolute;
}
.for-string{
  position: absolute;
}
.return{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.return button{
  width: 80px;
  height: 40px;
  border-radius: 12px;
  border: none;
  background-color: rgb(110, 163, 241);
  font-size: 16px;
  font-weight: 500;
  color: black;
  outline: none;
  cursor: pointer;
}
.return button:hover{
  background:rgb(167, 197, 241);
  box-shadow: 0px 0px 7px 1px rgba(55, 128, 238, 0.8);
  color: rgb(30, 71, 119);
}



.table::-webkit-scrollbar{
  width: 7px;
}
.table::-webkit-scrollbar-track{
  background: #E4EDF5;
  border-radius: 100px;
}
.table::-webkit-scrollbar-thumb{
   width: 5px;
   border: 1px solid #E4EDF5;
   background: #8799AB;
   border-radius: 150px;  
}

.input-group{
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}
.input-group label{
  align-self: center;
}
.uncorrect{
  color: red;
}
.attention{
  border: 1px solid orange !important;
}
.form-warning{
  text-align: center;
  color: red;
}